
require('./searchResult.less')
import {searchResult} from '../../data/data.js'

import GoodGrid from '../../components/GoodGrid.vue'
import EndingTip from '../../components/EndingTip.vue'
import Search from 'vux/src/components/search/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


export default {
components: {
Search,
GoodGrid,
EndingTip,
ViewBox
},
data() {
return {
searchQuery: '',
results: [],
value: '',
defaultValue: 'hi',
resultData: [],
}
},
methods: {
resultClick(item) {
this.searchQuery = item.title
this.resultData = searchResult
},
getResult(val) {
this.results = val
? this._getResult(this.value)
: this._getResult(this.defaultValue)
},
_getResult (val) {
let rs = []
for (let i = 0; i < 10; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
})
}
return rs
},
backHandler(){
this.$router
? this.$router.back()
: window.history.back()
}
},

}




