
require('./addressEdit.less')
import XInput from 'vux/src/components/x-input/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XTextarea from 'vux/src/components/x-textarea/index.vue'


export default {
components: {
XInput,
XAddress,
XTextarea
},
data() {
return {
ad: {},
value: [],
addressData: ChinaAddressData,

}
},
computed: {
hasSave() {
return this.$store.getters.hasSave
}
},
watch: {
hasSave: function(val, oldVal) {

console.log(val, oldVal)
if(val){
this.$store.dispatch({
type: 'changeHasSave'
})
this.$router
? this.$router.back()
: window.history.back()
}
}
},
created(){
console.log(this.$route.params.ad)
this.ad = this.$route.params.ad || {
name: '',
tel: '',
address: ''
}
},
methods: {
changeName (val) {
console.log(val)
this.ad.name = val
},
changeTel (val) {
console.log(val)
this.ad.tel = val
},
changeAddress (val) {
console.log(val)
this.ad.address = val
},
submitHandle(){
console.log(this.value)
this.$store.dispatch({
type: 'editAddress',
address: {
name: this.ad.name,
tel: this.ad.tel,
address: this.ad.address,
cityCode: this.value
}
})
}
}
}



