
import Popup from 'vux/src/components/popup/index.vue'


export default {
props: ['share','shareCls'],
components: {
Popup
},
data() {
return {
showShare: false
}
},
methods: {
showSharePop(){
this.showShare = !this.showShare
},
closeSharePop(){
this.showShare = !this.showShare
}
}
}



