export default {
	props: {
		datas: {
			type: null
		},
		scrollerItemStyle: {
			type: Object,
			default: function() {
				return {
					width: 0,
					marginLeft: 0,
					surplus: 0
				}
			}
		}
	},
	data() {
		return {
			// scrollerItemStyle: {
			// 	width: 200,
			// 	marginLeft: 10
			// }
		}
	},
	computed: {
		scrollerBoxStyleWidth: function() {
			return this.datas.length * (this.scrollerItemStyle.width + this.scrollerItemStyle.marginLeft) - this.scrollerItemStyle.marginLeft + this.scrollerItemStyle.surplus
		}
	}
}



