export default {
	props: ['item'],
	data() {
		return {
			selected: -1,
			selectedText: ''
		}
	},
	methods: {
		paramSelect(val, index) {
			this.selected = index
			this.selectedText = val 
			this.$emit('paramChange', {name: this.item.name, val})
			// this.$store.dispatch({
			// 	type: 'selectParams',
			// 	val
			// })
		}
	}
}



