
require('./index.less')


import SharePopup from '../../components/sharePopup.vue'
import CatBox from '../../components/catBox.vue'
import SaleFloor from '../../components/SaleFloor.vue'
import EndingTip from '../../components/EndingTip.vue'
import Recommend from '../../components/Recommend.vue'
import ScrollerBox from '../../components/ScrollerBox.vue'
import GoodGrid from '../../components/GoodGrid.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'




export default {
components: {
Popup,
CatBox,
SaleFloor,
EndingTip,
Recommend,
GoodGrid,
Swiper,
SwiperItem,
ScrollerBox,
SharePopup,
ViewBox
},
data() {
return {
shop: {},
shopBanner: [],
shopCat: [],
floor1: [],
recommendGoods: []
}
},
created(){

this.getdata();

},
computed: {






},
methods: {
getdata()
{
let _this=this;
_this.$api({
method: 'get',
url: '/values'
}).then((response) => {

_this.shop=response.data.shop;
_this.shopBanner=response.data.shopBanner;
_this.shopCat=response.data.shopCat;
_this.floor1=response.data.floor1;
console.log( response.data);
_this.recommendGoods=response.data.recommendGoods;


})


}



}
}



