
require('./commentPost.less')
import {commentPost} from '../../data/data.js'

import CommentPostCard from '../../components/commentPostCard.vue'
import GoodList from '../../components/GoodList.vue'
import Rater from 'vux/src/components/rater/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


export default {
components: {
Rater,
GoodList,
XTextarea,
ViewBox,
CommentPostCard
},
data() {
return {

}
},
computed: {
goods() {
console.log(this.$store.getters.commentGoods)
return this.$store.getters.commentGoods
},
rateTips() {
return this.$store.getters.rateTips
}
},
created() {
this.$store.dispatch({
type: 'getCommentPost'
})
},
methods: {
postCommentHandle() {
this.$store.dispatch({
type: 'submitComments'
})
}
}
}



