
import Rater from 'vux/src/components/rater/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
props: ['comment', 'type'],
components: {
Rater,
Cell
},
computed: {
cardStyle() {
switch(this.type){
case 'part':
return 'comment-card-part'
break;
case 'all':
return 'comment-card-all'
break;
}
}
}
}



