
import GoodList from './GoodList.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Rater from 'vux/src/components/rater/index.vue'


export default {
props: ['data', 'rateTips'],
components: {
Rater,
XTextarea,
GoodList
},
data() {
return {
rate: 5,
rateTip: this.rateTips.level5
}
},
watch: {
rate(newVal, oldVal) {
console.log(this.rateTips)
console.log(this.rate)
switch(this.rate){
case 5:
this.rateTip = this.rateTips.level5
break;
case 4:
this.rateTip = this.rateTips.level4
break;
case 3:
this.rateTip = this.rateTips.level3
break;
case 2:
this.rateTip = this.rateTips.level2
break;
case 1:
this.rateTip = this.rateTips.level1
break;
case 0:
this.rateTip = '写点什么吧'
break;
}
}
},
methods: {
changeComment(val) {
console.log(val)
this.$store.dispatch({
type: 'setonecomment',
comment:{
id: 'x',
content: val
}
})
}
}
}



