
require('./orderList.less')
import {myOrders} from '../../data/data.js'

import EmptyView from '../../components/emptyView.vue'
import PayWay from '../../components/payWayPopup.vue'
import ShopCard from '../../components/shopCard.vue'
import GoodList from '../../components/GoodList.vue'
import EndingTip from '../../components/EndingTip.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
components: {
EmptyView,
Tab,
TabItem,
GoodList,
XButton,
EndingTip,
ViewBox,
ShopCard,
PayWay
},
data() {
return {
list: [
{
tag: 'all',
text: '全部'
},
{
tag: 'needPay',
text: '待付款'
},
{
tag: 'needSend',
text: '待发货'
},
{
tag: 'needGet',
text: '待收货'
},
{
tag: 'needComment',
text: '待评价'
},
{
tag: 'needService',
text: '售后'
}
],
index: 0,

}
},
created() {
this.initOrders()
},
computed: {
computedTag() {
return this.$route.query.tag
},
ComputedState() {

},
loading() {
return this.$store.getters.loading
},
orderList() {
return this.$store.getters.orders
}
},
methods: {
payBtnHandler(){

},
tabHandler(tag){

















this.$store.dispatch({
type: 'changeOrderTag',
tag
})
},
initOrders() {
this.$store.dispatch('getOrders')
}
}
}



