
import Icon from 'vux/src/components/icon/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'


export default {
props: ['data'],
components: {
Icon,
XNumber
},
data() {
return {
num: 1,
selected: false
}
},
methods: {
selectHandler(){
this.selected = !this.selected
this.$emit('on-selected-good')
},
change(val) {
this.$emit('on-selected-num',val)
}
}
}



