},
pay: {
tag: 'pay',
text: '待付款',
icon: 'zui-icon zui-icon-payment'
},
send: {
tag: 'send',
text: '待发货',
icon: 'zui-icon zui-icon-shipped'
},
get: {
tag: 'get',
text: '待收货',
icon: 'zui-icon zui-icon-the-receipt'
},
comment: {
tag: 'comment',
text: '待评价',
icon: 'zui-icon zui-icon-comment'
},
sole: {
tag: 'sole',
text: '售后/退款',
icon: 'zui-icon zui-icon-KEFU'
}
}
}
}
}



