
require('./articleList.less')
import {articles,articleBanner} from '../../data/data.js'

import EndingTip from '../../components/EndingTip.vue'
import ArticleCard from '../../components/articleCard.vue'
import Card from 'vux/src/components/card/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'



export default {
components: {
EndingTip,
ArticleCard,
Swiper,
SwiperItem,
ViewBox,
Loading
},
data() {
return {

}
},
created(){
this.initArticles()
},
computed: {
articles(){
return this.$store.getters.articles
},
banners(){
return this.$store.getters.banners
},
loading(){
return this.$store.getters.loading
}
},
methods: {
initArticles(){
this.$store.dispatch('getArticles')
},

}

}



