export default {
	props: ['coupon'],
	computed: {
		couponState() {
			switch(this.coupon.state){
			case 'usefull':
				return {
					type: 'ok',
					stateCls: 'coupon-unuse',
					stateImg: '/static/img/coupon-unuse.png'
				}
				break;
			case 'unusefull':
				return {
					type: 'no',
					stateCls: 'coupon-used',
					stateImg: '/static/img/coupon-used.png'
				}
				break;
			case 'timeout':
				return {
					type: 'used',
					stateCls: 'coupon-timeout',
					stateImg: '/static/img/coupon-timeout.png'
				}
				break;
			defaul:
				return {}
				break;
			}
		}
	}
}



