
require('./articleDetail.less')
import {article,articleRecommend,articleGoods} from '../../data/data.js'

import SharePopup from '../../components/sharePopup.vue'
import Recommend from '../../components/Recommend.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


export default {
components: {
Recommend,
ViewBox,
SharePopup
},
data() {
return {
likeState: false,
likeCls:{
unlikeCls: 'zui-icon-zan-before',
likedCls: 'zui-icon-zan-after'
},
shareInfo: {
qrcode: '/static/img/good-default.jpg',
text: '长按二维码分享'
}
}
},
created() {
this.initArticle()
},
computed: {
loading(){
return this.$store.getters.loading
},
article(){
return this.$store.getters.article
},
recommendArticles(){
return this.$store.getters.recArtArticles
},
recommendGoods(){
return this.$store.getters.recArtGoods
}
},
methods: {
likeHandler() {
if(this.likeState) {
return
}else{
this.likeState = true
this.article.like ++
}
},
initArticle(){
this.$store.dispatch('getArticle')
}
}
}



