
require('./goodCat.less')
import {catTags,catBanner,catResult} from '../../data/data.js'

import ScrollerBox from '../../components/ScrollerBox.vue'
import EndingTip from '../../components/EndingTip.vue'
import GoodGrid from '../../components/GoodGrid.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'


export default {
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
ViewBox,
GoodGrid,
Scroller,
EndingTip,
ScrollerBox
},
data() {
return {
index: 0,
tag: '',
ItemSelected: false,



}
},
created() {
this.initCat()
},
computed: {
computedTag() {
return this.$route.query.tag
},
loading() {
return this.$store.getters.loading
},
catTags() {
return this.$store.getters.catTags
},
catBanner() {
return this.$store.getters.catBanner
},
catResult() {
return this.$store.getters.catResult
}
},
methods: {
catBarClick() {

},
initCat() {
this.$store.dispatch('getCatResult')
}
}
}



