
require('./cart.less')
import {myLikes} from '../../data/data.js'

import EmptyView from '../../components/emptyView.vue'
import CartCard from '../../components/cartCard.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
components: {
EmptyView,
Icon,
CartCard,
Swipeout,
SwipeoutItem,
SwipeoutButton,
},
data() {
return {
selectList: [],
num: 1,
selectAll: false,
cart: []
}
},
computed: {
loading(){
return this.$store.getters.loading
}
},
methods: {
handleEvents (type) {
console.log('event: ', type)
},
selectAllHandler() {
this.selectAll = !this.selectAll

},
selectGood(index){
if(this.selectList.includes(index)){
this.selectList.splice(this.selectList.indexOf(index),1)
}else{
this.selectList.push(index)
}

},
selectNum(){

}
}
}



