
require('./goodDetail.less')
import {goodDetail} from '../../data/data.js'

import GoodParam from '../../components/goodparam.vue'
import SharePopup from '../../components/sharePopup.vue'
import CommentCard from '../../components/commentCard.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'


export default {
components: {
Popup,
Scroller,
Toast,
Swiper,
SwiperItem,
Cell,
CommentCard,
Flexbox,
FlexboxItem,
ViewBox,
XSwitch,
XNumber,
SharePopup,
GoodParam
},
data() {
return {
selectParams: [],
isActive: false,
btnType: '',
show: false,
showToast: false,
isLike: false,
shareInfo: {
qrcode: '/static/img/good-default.jpg',
text: '长按二维码分享'
},
kefu: {
qrcode: '/static/img/good-default.jpg',
text: '长按二维码分享'
},

}
},
computed: {
scrollerHeight() {
return 'cale(100%-130px)'
},
good() {
return this.$store.getters.good
}
},
created() {
this.$store.dispatch({
type: 'getGoodDetail',
id: 'xx'
})
},
methods: {
likeHandler($event) {
console.log($event)
if(this.isLike){
this.isLike = false
this.good.likeNum--
}else{
this.isLike = true
this.good.likeNum++
}
},
resetScroller() {
this.$nextTick(() => {
this.$refs.scroller.reset()
})
},
log(str) {
console.log(str)
},
toggerSpeciPopup(type) {
switch(type){
case 0:
this.btnType = 0
this.show = !this.show
break;
case 1:
this.btnType = 1
this.show = !this.show
break;
case 2:
this.btnType = 2
this.show = !this.show
break
}
},
paramChange(param) {

var pos = this.selectParams.indexOf(param.name)
console.log(pos)
if(pos > -1){
this.selectParams[pos + 1] = param.val
}else{
this.selectParams.push(param.name, param.val)
}

console.log(this.selectParams)
},
handleBuy() {
console.log(1111)
this.show = false
}
}
}



