
require('./myCoupon.less')

import EmptyView from '../../components/emptyView.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import CouponCard from '../../components/couponCard.vue'
import EndingTip from '../../components/EndingTip.vue'

export default {
components: {
EmptyView,
ViewBox,
Tab,
TabItem,
CouponCard,
EndingTip
},
data() {
return {
index: 0
}
},
computed: {
loading(){
return this.$store.getters.loading
},
computedTag() {
return this.$route.query.tag
},
couponTag() {
return this.$store.getters.couponTag
},
coupons() {
return this.$store.getters.coupons
}
},
created(){
this.initCoupon()
},
methods: {
tabHandler(item){
switch(item.tag){
case 'usefull':
this.$store.dispatch('getUsefullCoupon')
break;
case 'unusefull':
this.$store.dispatch('getUnusefullCoupon')
break;
case 'timeout':
this.$store.dispatch('getTimeoutCoupon')
break;
}
},
initCoupon(){
this.$store.dispatch('getUsefullCoupon')
}
}
}



