
require('assets/css/common.less')
import ButtomNav from './components/BottomNav.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'



export default {
components: {
ButtomNav,
ViewBox,
Loading
},
data() {
return {
"enterAnimate": "", //页面进入动效
"leaveAnimate": "" //页面离开动效
}
},
watch: {
'$route' (to, from) {
const toDepth = to.path.split('/').length
const fromDepth = from.path.split('/').length
if(toDepth === 2) {

}
if(toDepth === fromDepth) {
return
}
this.enterAnimate = toDepth > fromDepth
? 'animated fadeInRight'
: 'animated fadeInLeft'

this.leaveAnimate = toDepth > fromDepth
? 'animated fadeOutRight'
: 'animated fadeOutLeft'





}
},
computed: {
loading(){
return this.$store.getters.loading
}
},
methods: {

},

}



