
require('./goodPay.less')
import {payPill, userAddress} from '../../data/data.js'

import ShopCard from '../../components/shopCard.vue'
import AddressCard from '../../components/addressCard.vue'
import GoodList from '../../components/GoodList.vue'
import PayPopup from './payPopup.vue'
import Cell from 'vux/src/components/cell/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'


export default{
components: {
ShopCard,
AddressCard,
GoodList,
Cell,
ViewBox,
Popup,
Scroller,
Checklist,
XInput,
PayPopup
},
data() {
return {
popupData: [],
paySumAmount: 0,
showPopup: false,
popupType: '',
couponTip: '无可用优惠券',
userAddress: userAddress,
payPill: payPill
}
},
computed: {
defaultAddress(){
return this.userAddress.filter(i => i.isDefault === 1)
}
},
methods: {
selectAddress(type){
this.popupType = type
this.showPopup = !this.showPopup
this.popupData = this.userAddress
},
selectCoupon(type,id) {
this.popupType = type
this.showPopup = !this.showPopup
this.payPill.forEach(i => {
if(i.id === id){
console.log(i.coupon)
this.popupData = i.coupon
}
})
},
resetScroller() {



},
log(str) {
console.log(str)
},
change (val) {
console.log('change', val)
}
}
}



