import GoodList from './GoodList.vue'
export default {
	props: ['shop','tip', 'iconTip'],
	components: {
		GoodList
	},
	computed: {
		state(){
			switch(this.shop.state){
				case 'needPay':
					return '待付款'
					break;
				case 'needSend':
					return '待发货'
					break;
				case 'needGet':
					return '待收货'
					break;
				case 'needComment':
					return '待评价'
					break;
				case 'needService':
					return '退款中'
					break;
			}
		}
	}
}	



